'use client'

import { useState, useRef } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Slider } from '@/components/ui/slider'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Textarea } from '@/components/ui/textarea'

interface TimelineMarker {
  id: string
  time: number
  type: 'text' | 'effect' | 'music'
  content: string
}

interface ProjectSettings {
  duration: number
  resolution: string
  frameRate: number
  audioEnabled: boolean
}

const EditorPage = () => {
  const [project, setProject] = useState<ProjectSettings>({
    duration: 30,
    resolution: '1080p',
    frameRate: 30,
    audioEnabled: true
  })
  
  const [timeline, setTimeline] = useState<TimelineMarker[]>([])
  const [currentTime, setCurrentTime] = useState(0)
  const [isPlaying, setIsPlaying] = useState(false)
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false)
  const [selectedFile, setSelectedFile] = useState<File | null>(null)
  const [textOverlay, setTextOverlay] = useState('')
  const [selectedEffect, setSelectedEffect] = useState('none')
  const [musicVolume, setMusicVolume] = useState([50])
  
  const fileInputRef = useRef<HTMLInputElement>(null)

  const effects = [
    { value: 'none', label: 'No Effect' },
    { value: 'blur', label: 'Blur' },
    { value: 'vintage', label: 'Vintage' },
    { value: 'blackwhite', label: 'Black & White' },
    { value: 'sepia', label: 'Sepia' },
    { value: 'neon', label: 'Neon Glow' }
  ]

  const musicTracks = [
    { value: 'none', label: 'No Music' },
    { value: 'upbeat', label: 'Upbeat Pop' },
    { value: 'chill', label: 'Chill Vibes' },
    { value: 'dramatic', label: 'Dramatic' },
    { value: 'ambient', label: 'Ambient' }
  ]

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const validTypes = ['video/mp4', 'video/webm', 'image/jpeg', 'image/png']
      if (!validTypes.includes(file.type)) {
        alert('Please upload a valid video (MP4, WebM) or image (JPEG, PNG) file.')
        return
      }
      
      setSelectedFile(file)
      setHasUnsavedChanges(true)
    }
  }

  const addTimelineMarker = (type: 'text' | 'effect' | 'music', content: string) => {
    const newMarker: TimelineMarker = {
      id: Date.now().toString(),
      time: currentTime,
      type,
      content
    }
    
    setTimeline([...timeline, newMarker])
    setHasUnsavedChanges(true)
  }

  const removeTimelineMarker = (id: string) => {
    setTimeline(timeline.filter(marker => marker.id !== id))
    setHasUnsavedChanges(true)
  }

  const handlePlayPause = () => {
    setIsPlaying(!isPlaying)
    // In a real app, this would control video/audio playback
  }

  const handleTimelineClick = (e: React.MouseEvent<HTMLDivElement>) => {
    const rect = e.currentTarget.getBoundingClientRect()
    const clickX = e.clientX - rect.left
    const newTime = (clickX / rect.width) * project.duration
    setCurrentTime(Math.max(0, Math.min(newTime, project.duration)))
  }

  const handleSave = async () => {
    try {
      // In a real app, this would save the project
      await new Promise(resolve => setTimeout(resolve, 1000))
      setHasUnsavedChanges(false)
      alert('Project saved successfully!')
    } catch (error) {
      alert('Failed to save project. Please try again.')
    }
  }

  const handleExport = async () => {
    try {
      // In a real app, this would export the final video
      await new Promise(resolve => setTimeout(resolve, 2000))
      alert('Video exported successfully!')
    } catch (error) {
      alert('Failed to export video. Please try again.')
    }
  }

  return (
    <div className="max-w-6xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-bold mb-2">Content Editor</h1>
          <p className="text-muted-foreground">
            Create and edit your Twin AI content with professional tools
          </p>
        </div>
        
        <div className="flex space-x-2">
          {hasUnsavedChanges && (
            <span className="text-sm text-muted-foreground flex items-center">
              Unsaved changes
            </span>
          )}
          <Button variant="outline" onClick={handleSave}>
            Save Project
          </Button>
          <Button onClick={handleExport}>
            Export Video
          </Button>
        </div>
      </div>

      <div className="grid lg:grid-cols-3 gap-6">
        {/* Main Editor */}
        <div className="lg:col-span-2 space-y-4">
          {/* Preview Area */}
          <Card>
            <CardHeader>
              <CardTitle>Preview</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="bg-black rounded-lg aspect-video flex items-center justify-center mb-4">
                {selectedFile ? (
                  <div className="text-white text-center">
                    <p className="mb-2">Preview: {selectedFile.name}</p>
                    <p className="text-sm opacity-75">
                      {selectedFile.type.startsWith('video/') ? 'Video' : 'Image'} Content
                    </p>
                  </div>
                ) : (
                  <p className="text-white/50">Upload content to preview</p>
                )}
              </div>
              
              {/* Playback Controls */}
              <div className="flex items-center justify-center space-x-4">
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handlePlayPause}
                  disabled={!selectedFile}
                >
                  {isPlaying ? 'Pause' : 'Play'}
                </Button>
                <span className="text-sm text-muted-foreground">
                  {currentTime.toFixed(1)}s / {project.duration}s
                </span>
              </div>
            </CardContent>
          </Card>

          {/* Timeline */}
          <Card>
            <CardHeader>
              <CardTitle>Timeline</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {/* Timeline Bar */}
                <div
                  className="relative bg-muted rounded-lg h-16 cursor-pointer"
                  onClick={handleTimelineClick}
                >
                  {/* Time markers */}
                  <div className="absolute inset-0 flex items-center">
                    {Array.from({ length: Math.ceil(project.duration / 5) + 1 }, (_, i) => (
                      <div
                        key={i}
                        className="absolute text-xs text-muted-foreground"
                        style={{ left: `${(i * 5 / project.duration) * 100}%` }}
                      >
                        {i * 5}s
                      </div>
                    ))}
                  </div>
                  
                  {/* Current time indicator */}
                  <div
                    className="absolute top-0 bottom-0 w-0.5 bg-primary"
                    style={{ left: `${(currentTime / project.duration) * 100}%` }}
                  />
                  
                  {/* Timeline markers */}
                  {timeline.map((marker) => (
                    <div
                      key={marker.id}
                      className="absolute top-1 bottom-1 w-2 bg-secondary rounded cursor-pointer"
                      style={{ left: `${(marker.time / project.duration) * 100}%` }}
                      onClick={(e) => {
                        e.stopPropagation()
                        removeTimelineMarker(marker.id)
                      }}
                      title={`${marker.type}: ${marker.content}`}
                    />
                  ))}
                </div>

                {/* Timeline Controls */}
                <div className="flex items-center space-x-4">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => addTimelineMarker('text', textOverlay)}
                    disabled={!textOverlay.trim()}
                  >
                    Add Text
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => addTimelineMarker('effect', selectedEffect)}
                    disabled={selectedEffect === 'none'}
                  >
                    Add Effect
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => setCurrentTime(0)}
                  >
                    Reset
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Tools Panel */}
        <div className="space-y-4">
          {/* File Upload */}
          <Card>
            <CardHeader>
              <CardTitle>Media Upload</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <Button
                  variant="outline"
                  className="w-full"
                  onClick={() => fileInputRef.current?.click()}
                >
                  Upload Video/Image
                </Button>
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="video/*,image/*"
                  onChange={handleFileUpload}
                  className="hidden"
                />
                {selectedFile && (
                  <p className="text-sm text-muted-foreground">
                    Selected: {selectedFile.name}
                  </p>
                )}
              </div>
            </CardContent>
          </Card>

          {/* Editing Tools */}
          <Tabs defaultValue="text" className="w-full">
            <TabsList className="grid w-full grid-cols-3">
              <TabsTrigger value="text">Text</TabsTrigger>
              <TabsTrigger value="effects">Effects</TabsTrigger>
              <TabsTrigger value="audio">Audio</TabsTrigger>
            </TabsList>

            <TabsContent value="text">
              <Card>
                <CardHeader>
                  <CardTitle>Text Overlay</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="text-content">Text Content</Label>
                    <Textarea
                      id="text-content"
                      placeholder="Enter text to overlay..."
                      value={textOverlay}
                      onChange={(e) => setTextOverlay(e.target.value)}
                      rows={3}
                    />
                  </div>
                  
                  <div className="space-y-2">
                    <Label>Font Size</Label>
                    <Slider
                      defaultValue={[16]}
                      max={48}
                      min={12}
                      step={2}
                      className="w-full"
                    />
                  </div>
                  
                  <Button
                    className="w-full"
                    onClick={() => addTimelineMarker('text', textOverlay)}
                    disabled={!textOverlay.trim()}
                  >
                    Add to Timeline
                  </Button>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="effects">
              <Card>
                <CardHeader>
                  <CardTitle>Visual Effects</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="space-y-2">
                    <Label>Effect Type</Label>
                    <Select value={selectedEffect} onValueChange={setSelectedEffect}>
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        {effects.map((effect) => (
                          <SelectItem key={effect.value} value={effect.value}>
                            {effect.label}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  
                  <div className="space-y-2">
                    <Label>Intensity</Label>
                    <Slider
                      defaultValue={[50]}
                      max={100}
                      min={0}
                      step={5}
                      className="w-full"
                    />
                  </div>
                  
                  <Button
                    className="w-full"
                    onClick={() => addTimelineMarker('effect', selectedEffect)}
                    disabled={selectedEffect === 'none'}
                  >
                    Add to Timeline
                  </Button>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="audio">
              <Card>
                <CardHeader>
                  <CardTitle>Audio Settings</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="space-y-2">
                    <Label>Background Music</Label>
                    <Select defaultValue="none">
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        {musicTracks.map((track) => (
                          <SelectItem key={track.value} value={track.value}>
                            {track.label}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                  
                  <div className="space-y-2">
                    <Label>Music Volume</Label>
                    <Slider
                      value={musicVolume}
                      onValueChange={setMusicVolume}
                      max={100}
                      min={0}
                      step={5}
                      className="w-full"
                    />
                    <p className="text-sm text-muted-foreground">
                      Volume: {musicVolume[0]}%
                    </p>
                  </div>
                  
                  <Button
                    className="w-full"
                    onClick={() => addTimelineMarker('music', `Volume: ${musicVolume[0]}%`)}
                  >
                    Add to Timeline
                  </Button>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>

          {/* Project Settings */}
          <Card>
            <CardHeader>
              <CardTitle>Project Settings</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label>Duration (seconds)</Label>
                <Input
                  type="number"
                  value={project.duration}
                  onChange={(e) => setProject({
                    ...project,
                    duration: parseInt(e.target.value) || 30
                  })}
                  min={5}
                  max={300}
                />
              </div>
              
              <div className="space-y-2">
                <Label>Resolution</Label>
                <Select
                  value={project.resolution}
                  onValueChange={(value) => setProject({ ...project, resolution: value })}
                >
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="720p">720p HD</SelectItem>
                    <SelectItem value="1080p">1080p Full HD</SelectItem>
                    <SelectItem value="4k">4K Ultra HD</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}

export default EditorPage
